<template>
  <div class="container">
    <b-card
      title="Title"
      :img-src="activeQuestion.source"
      img-alt="Image"
      img-top
      v-if="activeQuestion"
    >
      <b-card-text
        class="d-flex flex-column flex-md-row align-items-center evently"
      >
        <button
          class="btn btn-md btn-primary"
          type="button"
          @click="next(activeQuestion.id, 'carnivorous')"
        >
          Это плотоядная!
        </button>
        <button
          class="btn btn-md btn-info"
          type="button"
          @click="next(activeQuestion.id, 'herbivorous')"
        >
          Нет, это травоядная!
        </button>
      </b-card-text>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "test",
  data() {
    return {
      tryMode: true,
      activeQuestion: null,
      activeQuestionNo: -1,
      questions: []
    };
  },
  methods: {
    startQuiz() {
      this.questions = [
        {
          id: 1,
          source:
            "https://cs11.pikabu.ru/post_img/2019/02/27/8/1551270225127649968.jpg"
        },
        {
          id: 2,
          source:
            "https://cs13.pikabu.ru/post_img/2019/02/27/8/1551270304145087855.jpg"
        },
        {
          id: 3,
          source:
            "https://cs10.pikabu.ru/post_img/2019/02/27/8/1551270372174123922.jpg"
        }
      ];
      this.nextQuestion();
    },
    saveAnswer() {
      return;
    },
    nextQuestion() {
      this.activeQuestionNo++;
      if (this.questions.length > this.activeQuestionNo) {
        this.activeQuestion = this.questions[+this.activeQuestionNo];
      } else {
          this.$bvModal.show('modal-finish');
      }
    },
    // следующий вопрос
    next(id, answer) {
      console.log(id, answer);
      this.saveAnswer({
        id,
        answer
      });
      this.nextQuestion();
    }
  },
  beforeMount() {
    this.tryMode = this.$route.meta.tryMode;
    console.log('tryMode', this.tryMode);
    // Сохраняем список вопросов
    this.startQuiz();
  }
};
</script>

<style scoped>
.evently {
  justify-content: space-evenly;
}
</style>
